import * as React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, ButtonGroup, Button, Row, Col } from 'react-bootstrap';
import WineCard from '../../components/Card.js';
import { useLocalStorage } from 'react-use';


const dummyPrice = 59

function PosPage() {

    // window.localStorage['wine'] = 'Latte'
    const [wine, setWine] = useLocalStorage('wine', 'Latte')

    let [wineTitles, setWineTitles] = React.useState([])
    let [subMenu, setSubMenu] = React.useState('reds')
    // let [cartWine, setCartWine] = React.useState([])
    let [cartWine, setCartWine] = useLocalStorage('cartWine', [])

    function addToCard(wine) {
        // console.debug(wine)
        cartWine.push(wine)
        console.table(cartWine)
        setCartWine([...cartWine])
    }

    function clearCard(){
        setCartWine([])
    }

    React.useEffect(() => {
        let items = []
        fetch(`https://api.sampleapis.com/wines/${subMenu}`)
            .then(res => res.json())
            .then((wines) => {
                for (let i = 0; i < wines.length; i++) {
                    // console.log(wines[i].title)
                    items.push(
                        <WineCard
                            key={i}
                            image={wines[i].image}
                            title={wines[i].wine}
                            description={wines[i].winery}
                            price={dummyPrice}
                            handleClick={() => { addToCard(wines[i]) }}
                        />
                    )
                }
                setWineTitles(items)
            })
    }, [subMenu])


    return <Container>
        <h1>POS</h1>
        <ButtonGroup aria-label="Basic example">
            <Button variant="secondary" onClick={() => { setSubMenu('reds') }}>Reds</Button>
            <Button variant="secondary" onClick={() => { setSubMenu('whites') }}>Whites</Button>
            <Button variant="secondary" onClick={() => { setSubMenu('sparkling') }}>Sparkling</Button>
            <Button variant="secondary" onClick={() => { setSubMenu('rose') }}>Rose</Button>
            <Button variant="secondary" onClick={() => { setSubMenu('dessert') }}>Desert</Button>
            <Button variant="secondary" onClick={() => { setSubMenu('port') }}>Port</Button>

        </ButtonGroup>

        <Button variant="danger" onClick={() => {clearCard()}}>Clear CartWine</Button>

        <Row>
            <Col>
                <Row>
                    {wineTitles}
                </Row>
            </Col>
            <Col sm={3}>
                <h2>CartWine {wine}</h2>
                {cartWine.map((item, i) => {
                    return <Row key={i}>
                        <Col>{item.winery}</Col>
                        <Col>{dummyPrice}</Col>
                    </Row>
                })}
                <Row>
                    Total: {cartWine.length * dummyPrice} Baht
                </Row>
            </Col>
        </Row>
    </Container >
}

export default PosPage